.list {
  display: grid;
  grid-template-columns: repeat(6, 26px);
  grid-auto-rows: 26px;
  gap: 6px;
  list-style: none;
  margin: 0;
  padding: 0;
  place-content: center;
}

.item {
  cursor: pointer;
  border: 1px solid black;
}

.selected {
  border: 1px solid greenyellow;
}

@media (max-width: 768px) {
  .list {
    grid-template-columns: repeat(6, 50px);
    grid-auto-rows: 50px;
  }

  .img {
    width: 48px;
  }
}
